<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input placeholder="" clearable v-model="queryInfo.query">
            <el-button slot="append" icon="el-icon-search" @click="searchBtn"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="orderList" style="width: 100%" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="order_number" label=""></el-table-column>
        <el-table-column prop="order_price" label="" width="150px"></el-table-column>
        <el-table-column prop="order_pay" label="" width="100px">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.order_pay === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="100px"></el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">{{scope.row.create_time*1000|dateformat}}</template>
        </el-table-column>
        <el-table-column label="" width="150px">
          <template>
            <el-tooltip effect="dark" content="" placement="top">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="editDialogVisible = true"
              ></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top">
              <el-button
                type="success"
                icon="el-icon-location"
                size="mini"
                @click="showKuaidiDialog"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5,10,15,20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editAddressDialogClose"
    >
      <el-form
        :model="editAddressForm"
        :rules="editAddressFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="/" prop="address1">
          <el-cascader
            v-model="editAddressForm.address1"
            :options="cityData"
            :props="cityCascaderProps"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editAddressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="kuaidiDialogVisible"
      width="50%"
      @close="kuaidiDialogClose"
    >
      <!--  -->
      <el-timeline reverse>
        <el-timeline-item
          v-for="(activity, index) in  kuaidiData"
          :key="index"
          :timestamp="kuaidiData.ftime"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from '../../plugins/citydata.js'
export default {
  data() {
    return {
      // 
      orderList: [],
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10,
        user_id: '',
        pay_status: '',
        is_send: '',
        order_fapiao_title: '',
        order_fapiao_company: '',
        order_fapiao_content: '',
        consignee_addr: ''
      },
      // 
      total: 0,
      // 
      editDialogVisible: false,
      //  
      editAddressForm: {
        address1: [],
        address2: ''
      },
      // 
      editAddressFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cityData,
      // 
      cityCascaderProps: {
        expandTrigger: 'hover',
        value: 'value',
        lable: 'lable',
        children: 'children'
      },
      // 
      kuaidiDialogVisible: false,
      // 
      kuaidiData: []
    }
  },
  methods: {
    // 
    async getOrderList() {
      const { data: res } = await this.$http('orders', {
        params: this.queryInfo
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.orderList = res.data.goods
      this.total = res.data.total
      console.log(this.orderList, '')
    },
    // 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    searchBtn() {
      if (!this.queryInfo.query) {
        return this.$message.info('')
      }
      this.getOrderList()
    },
    // 
    editAddressDialogClose() {
      this.editAddressForm.address1 = []
      this.editAddressForm.address2 = ''
    },
    // 
    async showKuaidiDialog() {
      // 
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.kuaidiData = res.data
      console.log(this.kuaidiData)
      this.kuaidiDialogVisible = true
    },
    kuaidiDialogClose() {}
  },
  created() {
    this.getOrderList()
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>
